//import 라이브러리
import React from 'react';


const IoListItemOut = () => {
    /*---라우터 관련-------------------------------*/
    /*---상태관리 변수들(값이 변화면 화면 랜더링 )---*/
    /*---일반 변수--------------------------------*/
    /*---일반 메소드 -----------------------------*/
    /*---훅(useEffect)+이벤트(handle)메소드-------*/
    return (
        <div className="item-box">
            <table>
                <colgroup>
                    <col style={{width: '80px'}} />
                    <col />
                </colgroup>
                <tr>
                    <td rowSpan="3" className="itemImg-box">
                        <div className="itemImg-wrap">
                            <img src="/images/1.jpg" alt="상품이미지"/>
                        </div>
                        <div className="time">13:30</div> 
                    </td>
                    <td className="title_city-box">
                        <div className="city">태국 &gt; 방콕</div>
                        <div className="title">베트남 쌀국수</div>
                    </td>
                </tr>
                <tr>
                    <td className="price-box">
                        <div className="payMethod">현금</div>
                        <div class="minus">-999,999,999<span class="unit">USD</span></div>
                    </td>
                </tr>
                <tr>
                    <td className="subInfo-box">
                        <div>식비</div>
                        <div>99,999,999 KRW</div>   
                    </td>
                </tr>
            </table>
        </div>
        /* /item-box */
    );
}

export default IoListItemOut;
